import React from "react"
import { useStaticQuery, graphql } from "gatsby"

const Bio = () => {
  const data = useStaticQuery(graphql`
    query BioQuery {
      site {
        siteMetadata {
          author {
            name
            summary
          }
        }
      }
    }
  `)

  const { author } = data.site.siteMetadata
  return (
    <div style={{ display: `flex`, marginBottom: '10px', }}>
      <p>
        {author.summary}
      </p>
    </div>
  )
}

export default Bio
